import React, { useEffect } from 'react'

import { initializeApp } from "firebase/app";

import { getDatabase, ref, onValue, get, child, set } from "firebase/database";
import { Typography, Stack, Container, Paper, IconButton, ButtonGroup, Button } from '@mui/material';
import FavoriteRoundedIcon from '@mui/icons-material/FavoriteRounded';
import AddIcon from '@mui/icons-material/Add';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import { AlternateEmail } from '@mui/icons-material';
import Invoice from './Invoice'
function Received({me, removeHugs}) {
    const [open, setOpen] = React.useState(false);
    const [info, setInfo] = React.useState({ from: null, to: null });
    const handleClose = () => setOpen(false);
    const handleOpen = () => setOpen(true);
    function acquire(user, type) {
        removeHugs(me, user, type == 3)
        setOpen(true)
        setInfo({ from: user, to: me.name, type: type == 3 ? "Giant" : "Regular" })
    }
    return (<>
        <Paper sx={{ padding: 3 }} variant="outlined">
            <Stack spacing={2}>
                <Typography variant="h6">Received hugs: {Object.values(me.received).reduce((a, b) => a + b)}</Typography>
                {Object.keys(me.received).map(user =>
                    <Stack direction="row" spacing={2} alignItems="center">
                        <ButtonGroup>
                            <Button size="small" onClick={() => acquire(user, 1)} disabled={me.received[user] < 1} variant="outlined"><FavoriteRoundedIcon /></Button>
                            <Button size="normal" onClick={() => acquire(user, 3)} disabled={me.received[user] < 3} variant="contained" ><FavoriteRoundedIcon /></Button>
                        </ButtonGroup>

                        <Typography>{me.received[user]}{" "}from {user}</Typography>
                    </Stack>)}
            </Stack>
        </Paper>
        <Invoice open={open} handleClose={handleClose} info={info} />
    </>);
}
export default Received